// src/components/JoinSessionForm.tsx
import React, { useState } from 'react';
import styles from './JoinSessionForm.module.css';

interface JoinSessionFormProps {
  onJoin: (token: string) => void;
  onCancel: () => void;
}

const JoinSessionForm: React.FC<JoinSessionFormProps> = ({ onJoin, onCancel }) => {
  const [token, setToken] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!token.trim()) {
      setError('  ');
      return;
    }

    if (token.length < 10) {
      setError('  ');
      return;
    }

    onJoin(token.trim());
  };

  const extractTokenFromUrl = () => {
    const inputValue = token.trim();
    
    if (!inputValue) {
      setError('  URL     ');
      return;
    }

    //     URL
    const tokenRegex = /(?:[?&]token=|\btoken=)([^&]+)/i;
    const match = inputValue.match(tokenRegex);
    
    if (match && match[1]) {
      const extractedToken = match[1];
      setToken(extractedToken);
      setError(''); //     
    } else {
      // ,      
      const isLikelyToken = /^[a-zA-Z0-9_-]+$/.test(inputValue) && inputValue.length >= 10;
      
      if (isLikelyToken) {
        setError('    .   URL,  .');
      } else {
        setError('   . : ...?token=_');
      }
    }
  };

  return (
    <div className={styles.container}>
      <h3>    </h3>
      
      <p className={styles.description}>
          ,     .
      </p>

      <form onSubmit={handleSubmit} className={styles.form}>
        <div className={styles.inputGroup}>
          <label htmlFor="token"> :</label>
          <input
            type="text"
            id="token"
            value={token}
            onChange={(e) => {
              setToken(e.target.value);
              setError(''); //    
            }}
            placeholder="     ..."
            className={styles.input}
          />
          <button 
            type="button" 
            onClick={extractTokenFromUrl}
            className={styles.extractButton}
          >
              URL
          </button>
        </div>

        {error && <div className={styles.error}>{error}</div>}

        <div className={styles.buttonGroup}>
          <button type="button" onClick={onCancel} className={styles.cancelButton}>
            
          </button>
          <button type="submit" className={styles.joinButton}>
            
          </button>
        </div>
      </form>

      <div className={styles.hint}>
        <strong>  ?</strong>
        <ol>
          <li>       </li>
          <li>       </li>
          <li> "  URL"    </li>
        </ol>
      </div>
    </div>
  );
};

export default JoinSessionForm;