// src/hooks/useAutocomplete.ts -  
import { useState, useEffect, useCallback, useRef } from 'react';

interface UseAutocompleteOptions {
  delay?: number;
  maxSuggestions?: number;
  filterFn?: (item: string, query: string) => boolean;
}

export const useAutocomplete = (
  query: string,
  source: string[],
  options: UseAutocompleteOptions = {}
) => {
  const {
    delay = 150,
    maxSuggestions = 5,
    filterFn = (item, query) => item.toLowerCase().includes(query.toLowerCase())
  } = options;

  const [suggestions, setSuggestions] = useState<string[]>([]);
  const [isOpen, setIsOpen] = useState(false);
  const [debouncedQuery, setDebouncedQuery] = useState(query);
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  //  
  useEffect(() => {
    //   
    if (timerRef.current) {
      clearTimeout(timerRef.current);
    }

    //   
    timerRef.current = setTimeout(() => {
      setDebouncedQuery(query);
    }, delay);

    //  
    return () => {
      if (timerRef.current) {
        clearTimeout(timerRef.current);
      }
    };
  }, [query, delay]);

  //  
  useEffect(() => {
    if (debouncedQuery && source.length > 0) {
      const filtered = source
        .filter(item => filterFn(item, debouncedQuery))
        .slice(0, maxSuggestions);
      
      setSuggestions(filtered);
      setIsOpen(filtered.length > 0);
    } else {
      setSuggestions([]);
      setIsOpen(false);
    }
  }, [debouncedQuery, source, maxSuggestions, filterFn]);

  //   
  const selectSuggestion = useCallback((selected: string) => {
    // 1.   
    setSuggestions([]);
    setIsOpen(false);
    
    // 2.    (!)
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    
    // 3.  debouncedQuery    
    setDebouncedQuery(selected);
    
    // 4.   
    return selected;
  }, []);

  //   
  const closeSuggestions = useCallback(() => {
    // 1.   
    setIsOpen(false);
    setSuggestions([]);
    
    // 2.   
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    
    // 3.  debouncedQuery    
    setDebouncedQuery('');
  }, []);

  return {
    suggestions,
    isOpen,
    selectSuggestion,
    closeSuggestions,
  };
};