// src/components/ShareFlightModal.tsx
import React, { useState } from 'react';
import { supabase } from '../lib/supabaseClient';
import styles from './ShareFlightModal.module.css';

interface ShareFlightModalProps {
  userId: string;
  onClose: () => void;
  onShareCreated: (token: string) => void;
}

const ShareFlightModal: React.FC<ShareFlightModalProps> = ({ userId, onClose, onShareCreated }) => {
  const [permissions, setPermissions] = useState<'view' | 'edit'>('view');
  const [expiryDays, setExpiryDays] = useState<number>(7);
  const [generatedToken, setGeneratedToken] = useState<string>('');
  const [shareUrl, setShareUrl] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');

  const generateToken = () => {
    return Math.random().toString(36).substring(2, 15) + 
           Math.random().toString(36).substring(2, 15);
  };

  const createShareLink = async () => {
    try {
      setLoading(true);
      setError('');
      
      const token = generateToken();
      const expiresAt = new Date();
      expiresAt.setDate(expiresAt.getDate() + expiryDays);

      const { data, error } = await supabase
        .from('shared_sessions')
        .insert({
          owner_id: userId,
          token: token,
          permissions: permissions,
          expires_at: expiresAt.toISOString(),
          is_active: true
        })
        .select()
        .single();

      if (error) throw error;

      const url = `${window.location.origin}${window.location.pathname}?token=${token}`;
      setGeneratedToken(token);
      setShareUrl(url);
      onShareCreated(token);
      
    } catch (err: any) {
      setError(err.message || '   ');
      console.error('Error creating share link:', err);
    } finally {
      setLoading(false);
    }
  };

  const copyToClipboard = () => {
    navigator.clipboard.writeText(shareUrl)
      .then(() => alert(' !'))
      .catch(err => console.error('Copy failed:', err));
  };

  const deactivateLink = async () => {
    try {
      const { error } = await supabase
        .from('shared_sessions')
        .update({ is_active: false })
        .eq('token', generatedToken);

      if (error) throw error;
      alert(' ');
      onClose();
    } catch (err: any) {
      setError(err.message || '   ');
    }
  };

  return (
    <div className={styles.modalOverlay} onClick={onClose}>
      <div className={styles.modalContent} onClick={e => e.stopPropagation()}>
        <h3>   </h3>
        
        {/*   */}
        <div className={styles.hintBox}>
          <p> ,     </p>
          <p className={styles.hintSubtext}>
                     
          </p>
        </div>
        
        {!generatedToken ? (
          <>
            <div className={styles.formGroup}>
              <label> :</label>
              <div className={styles.radioGroup}>
                <label className={styles.radioLabel}>
                  <input
                    type="radio"
                    value="view"
                    checked={permissions === 'view'}
                    onChange={(e) => setPermissions(e.target.value as 'view' | 'edit')}
                    className={styles.radioInput}
                  />
                    
                  <span className={styles.radioDescription}>
                         
                  </span>
                </label>
                <label className={styles.radioLabel}>
                  <input
                    type="radio"
                    value="edit"
                    checked={permissions === 'edit'}
                    onChange={(e) => setPermissions(e.target.value as 'view' | 'edit')}
                    className={styles.radioInput}
                  />
                     
                  <span className={styles.radioDescription}>
                         
                  </span>
                </label>
              </div>
            </div>

            <div className={styles.formGroup}>
              <label>  :</label>
              <select
                value={expiryDays}
                onChange={(e) => setExpiryDays(Number(e.target.value))}
                className={styles.select}
              >
                <option value={1}>1 </option>
                <option value={7}>7  ( )</option>
                <option value={30}>30 </option>
                <option value={365}>1  ( )</option>
              </select>
              <p className={styles.selectHint}>
                      
              </p>
            </div>

            {error && <div className={styles.error}>{error}</div>}

            <div className={styles.buttonGroup}>
              <button onClick={onClose} className={styles.cancelButton}>
                
              </button>
              <button 
                onClick={createShareLink} 
                className={styles.createButton}
                disabled={loading}
              >
                {loading ? '...' : ' '}
              </button>
            </div>
          </>
        ) : (
          <>
            <div className={styles.successMessage}>
                   !
            </div>
            
            <div className={styles.shareInfo}>
              <div className={styles.infoRow}>
                <span className={styles.infoIcon}></span>
                <div>
                  <strong> :</strong> {permissions === 'view' ? ' ' : '  '}
                </div>
              </div>
              <div className={styles.infoRow}>
                <span className={styles.infoIcon}></span>
                <div>
                  <strong> :</strong>  {new Date(Date.now() + expiryDays * 24 * 60 * 60 * 1000).toLocaleDateString()}
                </div>
              </div>
              <div className={styles.infoRow}>
                <span className={styles.infoIcon}></span>
                <div>
                  <strong>:</strong>    ,     
                </div>
              </div>
            </div>

            <div className={styles.urlContainer}>
              <div className={styles.urlLabel}>  :</div>
              <input
                type="text"
                value={shareUrl}
                readOnly
                className={styles.urlInput}
                onClick={(e) => (e.target as HTMLInputElement).select()}
              />
              <button onClick={copyToClipboard} className={styles.copyButton}>
                  
              </button>
            </div>

            <div className={styles.finalHint}>
              <p> <strong>:</strong>        .</p>
              <p>    ,  .</p>
            </div>

            <div className={styles.buttonGroup}>
              <button onClick={deactivateLink} className={styles.deactivateButton}>
                  
              </button>
              <button onClick={onClose} className={styles.closeButton}>
                
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default ShareFlightModal;